<template xmlns="http://www.w3.org/1999/html">
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">EmployeeID: {{employee_id}}</li>
      <li class="collection-item">Department: {{dept}}</li>
      <li class="collection-item">Position: {{position}}</li>
    </ul>
    <router-link to="/" class="btn gray">Back</router-link>
    <button class="btn red" @click="deleteEmployee">Delete</button>
    <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}"
                 class="btn green">
      Edit
    </router-link>
  </div>
</template>

<script>
  import db from '../firebase/firebaseInit'
  export default {
    name: 'view-employee',
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    },
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id)
        .get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            })
          })
      })
    },
    watch: {
      '$router': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
          .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employee_id = doc.data().employee_id;
              this.name = doc.data().name;
              this.dept = doc.data().dept;
              this.position = doc.data().position;
            })
        })
      },
      deleteEmployee() {
        if (confirm('Are you sure?')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
            .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push('/');
            })
          })
        }
      }
    }
  }
</script>



